*{
  box-sizing: border-box;
}
body {
  margin: 0px;
  padding: 0px;
  font-family: sans-serif;
  background: #46529d;
  color:#fff
}
.text-center{
  text-align: center;
}
.container{
width: 60%;
margin: 10% auto;
}
.Listitems{
  background:#fff ;
  color:#46529d
}
.Listitems p{
  margin: 0px;
  padding: 10px 0px;
  text-align: center;
}
.Listitems span{
  display: inline-block;
  text-align: center;
  padding: 10px 0px;
  font-size: 20px;
  border: 0.3px solid #ccc ;
}
.Listitems span.title{
  background: #2ebaee;
  color:#fff
}
.Listitems span.name,
form input[type=text]{
  width:60%;
}
.Listitems span.age,
form input[type=number]{
  width:30%;
}
.Listitems span.action,
form input[type=submit]{
  width:10%;
}
.Listitems span.icon{
color:#f00;
cursor: pointer;
}
form input{
  padding :10px;
  background: #ebebeb;
  border: transparent;
  border:0.3px solid #ccc
}
form input[type=submit]{
  background: #2ebaee ;
  border:0.3px solid #2ebaee;
  color:#fff;
  cursor: pointer;
}

